import React, { Component } from 'react';
import { Provider } from 'react-redux';
import { createStore, applyMiddleware } from 'redux';
import { composeWithDevTools } from 'redux-devtools-extension';
import thunk from 'redux-thunk';
import { BrowserRouter as Router,	Route,	Link, Redirect } from 'react-router-dom';
import reducer from './reducers';
import ToDoList from './components/ToDoList';
import Calendar from './components/Calendar';
import Login from './components/Login';

const store = createStore(reducer, composeWithDevTools(applyMiddleware(thunk)));

class AppAll extends Component {
	render() {
		return (
		  <Router>
        <div>
          <Link id="toDoList_link" to="/app/to_do">ToDoList</Link>
          <Link id="calendar_link" to="/app/calendar">Calendar</Link>
          <Route path="/app/to_do" component={ToDoList}/>
          <Route path="/app/calendar" component={Calendar}/>
        </div>
      </Router>
		);
	}
}

class App extends Component {

  render() {
    return (
      <Provider store={store}>
        <Router>
          <div>
            <Route path="/app/to_do" component={AppAll} />
            <Route path="/login" component={Login}/>
            <Redirect from="/" to="/login"/>
          </div>
        </Router>
      </Provider>
    );
  }
}

export default App;



